import React, { Component } from 'react';
import { login } from '../../../rest/Rest';
import axios from 'axios';

export default class Login extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      password: ''
    }

    this.onLoginClick = this.onLoginClick.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
  }
  
  // componentDidMount() {
  //   this.restcall();
  // }
  
  // restcall() {
  //   console.log("called")
  //   getList()
  //   .then(function(response) {
  //     return response.json()
  //   }).then(function(json) {
  //     console.log('parsed json', json)
  //   }).catch(function(ex) {
  //     console.log('parsing failed', ex)
  //   })
  // }

  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  onLoginClick(id, password) {
    var FormData = require('form-data');
    var form = new FormData();
    form.append('login_email_mob', '9898989898');
    form.append('login_password', 'Test1234');
    //form.set('login_email_mob', '9898989898');
    //form.set('login_password', 'Test1234');
    
    // axios.post('http://localhost/ajpatel/api/login', {
    //   data: form,
    //   headers: {'Access-Control-Allow-Origin': '*' }
    // }).then(function(response) {
    //   console.log(response)
    // });
    login(form)
      .then(function(response) {
        console.log(response)
        return response.text();
      }).then(function(response) {
        console.log(response)
      }).catch(function(ex) {
        console.log('parsing failed', ex)
      })
  }
  
  render() {
    const { id, password } = this.state;
    
    return (
      <div>
      <input name="id" type="text" value={id} onChange={(event) => this.handleInputChange(event)}/>
      <input name="password" type="password" value={password} onChange={(event) => this.handleInputChange(event)}/>
      <button onClick={() => this.onLoginClick(id, password)}>Login</button>
      </div>
    );
  }
}


